<template>
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div>
            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{ $t('AddProductContainerList') }}</span>
                        <span class="p-actions float-right">
                            <RouterLink :to="'/Asset/AssetGrouping'" class="p-action-btn text-white float-right px-2 w-100">
                                <em class="fa fa-angle-double-left"></em><br>{{ $t('BacktoList') }}
                            </RouterLink>
                        </span>
                    </div>
                </div>
            </div>
        </div>
        <div class="border p-3">
            <div class="col-md-12 p-0" id="dynmicForm">
                <Form v-slot="{ values, meta }" ref="AssetAddGroupform">
                    <div class="row">
                        <div class="col-lg-4">
                            <div class="form-group">
                                <label>{{ $t('ConsumableItemType') }}<span class="text-danger">*</span></label>
                                <Field :name="$t('ConsumableItemType')" :rules="{ 'required': true }" v-slot="{ errors }"
                                    label="Select Product Type">
                                    <select v-model="AssetTypeId"
                                        v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                        @change="GetCompanyAssestCatalogs()">
                                        <option value=null>{{ $t('Select') }}</option>
                                        <option v-for="item in ProductTypelist" :value="item.Value" :key="item.ASSETTYPE">
                                            {{ item.Text }}</option>
                                    </select>
                                    <span v-if="errors[0]" :name="$t('ConsumableItemType')"
                                        class="invalid-feedback d-block">{{ errors[0] }}</span>
                                </Field>
                            </div>
                        </div>
                        <div class="col-lg-4">
                            <label>{{ $t('Location') }}<span class="text-danger">*</span></label>
                            <Field :name="$t('Location')" :rules="{ 'required': true }" v-slot="{ errors }">
                                <tree-view v-model="TreeValue" :options="options" placeholder="Select Location"
                                    :show-count="true" v-on:input="GetCompanyAssestCatalogs(TreeValue, AssetTypeId)"
                                    v-bind:class="{ 'is-invalid': errors[0] }">
                                </tree-view>
                                <span v-if="errors[0]" :name="$t('Location')" class="invalid-feedback d-block">{{
                                    errors[0] }}</span>
                            </Field>
                        </div>
                        <div class="col-lg-4">
                            <div class="form-group">
                                <label class="">{{ $t('SelectContainer') }}</label>:<span class="mandatory">*</span>
                                <Field :name="$t('SelectContainer')" :rules="{ 'required': true }" v-slot="{ errors }"
                                    label="Select Container">
                                    <select v-model="ContainerId" @change="setProductName(ContainerId)"
                                        v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }">
                                        <option value=null>{{ $t('Select') }}</option>
                                        <option v-for="item in ContainerData" :value="item.Value" :key="item.Value">
                                            {{ item.name }}</option>
                                    </select>
                                    <span v-if="errors[0]" :name="$t('SelectContainer')" class="invalid-feedback d-block">{{
                                        errors[0] }}</span>
                                </Field>
                            </div>
                        </div>
                        <div class="listing px-3">
                            <div class="table-responsive catalog_Row_MainBox" style="min-height: auto;">
                                <table id="tbContainer" class="table table-bordered dt-responsive nowrap mt-0"
                                    style="width:100%;">
                                    <thead class="thead-bg">
                                        <tr>
                                            <th style="width: 168px; min-width: 168px; max-width: 168px;"
                                                id="th-PRODUCTNAME" class="ui-resizable">
                                                <span href="javascript:;" style="min-width: 150px;">
                                                    {{ $t('AssetId') }}:<span class="mandatory d-inline-block">*</span>
                                                </span>
                                            </th>
                                            <th style="width: 156px; min-width: 156px; max-width: 156px;"
                                                id="th-COMPARTMENT" class="ui-resizable"><span href="javascript:;"
                                                    style="min-width: 138px;">{{ $t('Compartment') }}:<span
                                                        class="mandatory d-inline-block">*</span></span>
                                                <div id="th-COMPARTMENT-sizer" class="sizer"></div>
                                                <div class="ui-resizable-handle ui-resizable-e" style="z-index: 90;"></div>
                                            </th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(item, index) in NewDynamicField" :key="index">
                                            <td class="overflow-visible">
                                                <Field :name="'ProductName' + index" v-slot="{ errors }"
                                                    :rules="{ 'required': true }">
                                                    <select :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                        :name="item.name" v-model="item.ProductName"
                                                        @change="AlreadyExist($event, index, item, 'ProductName')"
                                                        class="form-control level">
                                                        <option value=null>{{ $t('Select') }}</option>
                                                        <option v-for="(item, index1) in ItemNamelist"
                                                            :value="item.ASSET_CATALOGUE_ID" :key="index1">
                                                            {{ item.NAME }}</option>
                                                    </select>
                                                    <span v-if="errors[0]" :name="'ProductName' + index"
                                                        class="invalid-feedback d-block">{{ 'The Product Name field is required' }}</span>
                                                </Field>

                                            </td>
                                            <td class="overflow-visible">

                                                <Field :name="'Compartment' + (index + 1)" :rules="{ 'required': true }"
                                                    v-slot="{ errors }">
                                                    <div class="input-group">

                                                        <select v-model="item.Compartment"
                                                            :name="'Compartment' + (index + 1)"
                                                            v-bind:class="{ 'form-control tdcm ddlCompartmentId': true, 'is-invalid': errors[0] }">
                                                            <option value=null>{{ $t('Select') }}</option>
                                                            <option v-for="(item, index) in CompartmentData"
                                                                :value="item.Value" :key="index">
                                                                {{ item.Text }}</option>
                                                        </select>
                                                        <span class="input-group-text border-0 bg-transparent p-0 pl-2">
                                                            <a @click="addRow" v-if="0 == parseInt(index)"
                                                                href="javascript:;"
                                                                class="plupload_button plupload_add add_asset_group_item_box round-icon-small btn-success-light"
                                                                title="" data-original-title="Add New Row"><i
                                                                    class="fa fa-plus pt-7 font-18"></i></a>
                                                            <a v-if="0 != parseInt(index)" @click="deleteRow(item)"
                                                                href="javascript:;"
                                                                class="round-icon-small btn-danger-light delete_asset_group_item_box float-left"
                                                                title="" checkprivilege="yes" data-groupid="0"
                                                                data-toggle-tooltip="tooltip"
                                                                data-original-title="Delete"><i class="fa fa-trash"></i></a>
                                                        </span>
                                                        <span v-if="errors[0]" :name="'Compartment' + (index + 1)"
                                                            class="invalid-feedback d-block">{{
                                                                'The Compartment field is required' }}</span>

                                                    </div>
                                                </Field>

                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                                <div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="row flex-row-reverse my-3">
                        <div class="col-lg-6 text-right">
                            <button v-if="IsVisibleSave" class="btn btn-success text-white mr-2"
                                @click="Save(values, meta)"><em class="fa fa-save pr-2"></em>{{ $t('Save') }}</button>

                            <a href="javascript:;" title="" class="btn btn-danger" @click="CancelButton"
                                data-toggle-tooltip="tooltip" data-original-title="Cancel"><em
                                    class="fa fa-close pr-2"></em>{{ $t('Cancel') }}</a>
                        </div>
                        <div class="col-lg-6">
                            <div class="py-2"><span class="mandatory">{{ $t('MandatoryString') }}</span></div>
                        </div>
                    </div>
                </Form>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from '../../service/DataService'
import Treeselect from '@riophae/vue-treeselect'
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
export default {
    components: {
        'tree-view': Treeselect
    },
    data() {
        return {
            AssetTypeId: null,
            ContainerName: '',
            ContainerId: null,
            AssetGroupId: 0,
            ChildRelatedInfoId: null,
            AssetCatalogQuantity: null,
            CompartmentId: 0,
            AssetCategoryId: 0,
            AssetCategoryName: null,
            ProductTypelist: [],
            ItemNamelist: [],
            LocationData: [],
            ContainerData: [],
            CompartmentData: [],
            NewDynamicField: [{
                Compartment: null,
                ProductName: null,
                count: 0
            }],
            AssetsGroupItemsList: [],
            options: [],
            containerName: '',
            IsVisibleSave: true,
            isLoading: false,
            TreeValue: null
        }
    },
    async created() {
        await this.GetProductTypeDropdown();
        await this.GetLocationByCompanyId();
    },
    methods: {
        async GetProductTypeDropdown() {
            var vm = this;
            await DataService.GetAssetContainer().then(response => {
                vm.ProductTypelist = response.data.Result;
            });
            // var url = `type=ITEMCODE&issueTo=&locationId=`;
            // DataService.GetAssetTypeCustom(url).then(response => {
            //     vm.ProductTypelist = JSON.parse(response.data);
            // })
        },
        async GetLocationByCompanyId() {
            var vm = this;
            var url = `reqForm=`;
            await DataService.GetLocationByCompanyId(url).then(function (response) {
                vm.FindalLocationArray = [];
                vm.LocationDataTree = JSON.parse(response.data).data;
                vm.LocationDataTree.forEach((value) => {
                    var CheckIfExist = vm.FindalLocationArray.filter(function (el) {
                        return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                    });
                    if (CheckIfExist.length == 0) {
                        if (value.PARENT_LOCATION_ID == null) {
                            let locationId = value.COMPANY_LOCATION_ID
                            let rslt = vm.getChildLocation(locationId, value, JSON.parse(response.data).data)
                            vm.FindalLocationArray.push(rslt)
                        }
                    }
                });
                vm.options = vm.FindalLocationArray;
            });
        },
        getChildLocation: function (locationId, currentItem, data) {
            let ths = this;
            let treeArray = {};
            treeArray = {
                id: currentItem.COMPANY_LOCATION_ID,
                label: currentItem.LOCATION_NAME
            }
            let retrunArray = treeArray;
            var childArray = data.filter(function (el) {
                return el.PARENT_LOCATION_ID == locationId;
            });
            let finalChildArray = [];
            childArray.forEach((value) => {
                let ChildfolderId = value.COMPANY_LOCATION_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.PARENT_LOCATION_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildLocation(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        treeArray = {
                            id: `${value.COMPANY_LOCATION_ID}`,
                            label: `${value.LOCATION_NAME}`
                        }
                        finalChildArray.push(treeArray);
                    }
                }
            });
            retrunArray.children = finalChildArray;
            return retrunArray;
        },
        async GetCompanyAssestCatalogs(locationId, assetId) {
            var vm = this;
            vm.isLoading = true;

            if (assetId == null) {
                vm.isLoading = false;
                return;
            }
            if (locationId == undefined) {
                vm.isLoading = false;
                return;
            }

            else {



                var url = `assetTypeId=${vm.AssetTypeId}&mode=&location_id=${locationId}`;
                await DataService.GetItemNameDropdown(url).then(function (response) {
                    vm.ContainerData = [];
                    response.data.Result.forEach((item, index) => {
                        var obj = {
                            name: item.Text,
                            Value: item.Value
                        }
                        vm.ContainerData.push(obj);
                    })
                });

            }
            await vm.GetItemNamesLocationwise(locationId);
            await vm.GetCompartmentName(vm.AssetTypeId);
            vm.isLoading = false;
        },
        async GetItemNamesLocationwise(LocationId) {
            var vm = this;
            vm.isLoading = true;
            vm.ItemNamelist = [];
            let url = `location_id=${LocationId}`;
            await DataService.BindItemLocationWise(url).then(response => {
                response.data.forEach((item, index) => {
                    var obj = {
                        NAME: item.NAME,
                        ASSET_CATALOGUE_ID: item.ASSET_CATALOGUE_ID
                    }
                    vm.ItemNamelist.push(obj);
                })
                vm.isLoading = false;
            })
        },


        async GetCompartmentName(asset_type_id) {
            var vm = this;
            vm.isLoading = true;
            var url = `asset_type_id=${asset_type_id}`
            await DataService.GetCompartmentName(url).then(
                response => {
                    vm.CompartmentData = response.data.Result
                }
            )
            vm.isLoading = false;

        },


        setProductName(item) {

            var vm = this;
            vm.containerName = '';
            vm.containerName = this.ContainerData.find(x => x.Value == item).name;

        },



        Save(values, meta) {
            var vm = this;
            vm.isLoading = true;
            var filterName = '';
            var ContainerList = [];
            vm.$refs.AssetAddGroupform.validate().then(result => {
                if (result) {
                    filterName = vm.ContainerData.find(x => x.Value == vm.ContainerId);
                    vm.ContainerName = filterName.name;
                    vm.NewDynamicField.forEach((item) => {
                        ContainerList.push({
                            AssetGroupId: 0,
                            ChildRelatedInfoId: item.ProductName,
                            AssetCatalogQuantity: 0,
                            CompartmentId: item.Compartment,
                            AssetCategoryId: item.ProductName,
                            AssetCategoryName: null
                        });
                    });
                    var obj = {
                        AssetTypeId: vm.AssetTypeId,
                        LocationId: vm.TreeValue,
                        ParentRelatedInfoId: vm.ContainerId,
                        AssetContainerName: vm.ContainerName,
                        StatusId: 0,
                        CreatedAt: null,
                        ModifiedAt: null,
                        CreatedBy: 0,
                        ModifiedBy: 0,
                        CompartmentId: 0,
                        IsUsed: null,
                        ContainerList: null,
                        ItemList: null,
                        AssetsGroupItemsList: ContainerList
                    }
                    DataService.AddAssetGrouping(obj).then(response => {
                        if (response.data.status == 0) {
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Product_Container')]), "success", true, vm.$t("Alert"));
                            vm.CancelButton();
                        } else {
                            vm.IsVisibleSave = false;
                        }
                        vm.isLoading = false;
                    });                   
                }
                vm.isLoading = false;
            });
        },
        addRow: function (e) {
            var vm = this;
            vm.$refs.AssetAddGroupform.validate().then(result => {
                if (result) {
                    vm.NewDynamicField.push({
                        ProductName: null,
                        Compartment: null,
                        count: vm.NewDynamicField.length + 1
                    })
                }
            })
        },

        deleteRow(index) {
            var vm = this;
            vm.confirmR(vm.$t('Please verify that you want to delete this record?'), true, false, function (result) {
                vm.NewDynamicField.splice(index, 1);
                return true;
            })
        },
        CancelButton() {
            this.$router.push({
                name: 'AssetGrouping'
            })
        },

        AlreadyExist: function (e, i, item, lbl) {
            var vm = this;
            var productName = vm.ItemNamelist.find(x => x.ASSET_CATALOGUE_ID == parseInt(item.ProductName)).NAME;
            if (vm.NewDynamicField.length == 1) {
                if (this.containerName.includes(productName) == true) {
                    vm.NewDynamicField.forEach(ele => {
                        if (vm.NewDynamicField.length == 1) {
                            ele.ProductName = ''
                        }
                        else {
                            //
                        }

                    })
                    vm.ShowAlert('Product name cannot contain container name (' + productName + '), please select some other value', "failure", vm.$t('Alert'))
                }
                else {
                    //
                }

            }
            else {
                ///
            }

        },
    },

}
</script>

<style scoped>
.vue-treeselect--searchable .vue-treeselect__input-container {
    padding: 0 !important;
}

.vue-treeselect__placeholder {
    color: #303030 !important;
}

.vue-treeselect__control-arrow {
    color: #000;
}

.vue-treeselect__placeholder {
    line-height: 24px;
}

#dynmicForm .vue-treeselect__control {
    border: none;
    height: 36px;
    padding: 0;
}
</style>
